import React, { useState } from 'react';
import Chart from 'react-apexcharts';
import FlowsC2CMatrix_green from '../../assets/data/FlowsC2CMatrix_green.json';

const CTCGreenWaterFlowChart = () => {
    const [selectedYear, setSelectedYear] = useState('2019');
    const [selectedSorting, setSelectedSorting] = useState('Import');
    const handleYearSelection = (e) => {
        setSelectedYear(e.target.value)
    }
    const handleSortingSelection = (e) => {
        setSelectedSorting(e.target.value)
    }


    const countriesData = FlowsC2CMatrix_green.map(country => ({
        name: country.CountryName,
        Export_Year: country[`Export_${selectedYear}`] / 1000000,
        Import_Year: country[`Import_${selectedYear}`] / 1000000,
    }));


    countriesData.sort((a, b) => b[`${selectedSorting}_Year`] - a[`${selectedSorting}_Year`]);

    // Extract top 5 countries and calculate total for other countries
    const topCountries = countriesData.slice(0, 10);
    const otherCountries_F_Total = countriesData.slice(10).reduce((total, country) => total + country.Export_Year, 0);
    const otherCountries_T_Total = countriesData.slice(10).reduce((total, country) => total + country.Import_Year, 0);

    // Create series data for chart
    const series = [
        {
            name: 'Virtual water transfer from AFG',
            data: topCountries.map(country => country.Export_Year).concat(otherCountries_F_Total),
            color: '#87A922'
        },
        {
            name: 'Virtual water transfer to AFG ',
            data: topCountries.map(country => country.Import_Year).concat(otherCountries_T_Total),
            color: '#114232'
        },
    ];

    // Create categories for x-axis including 'Other Countries' for the sum of other countries
    const categories = topCountries.map(country => country.name).concat('Other Countries');


    return (
        <>
        <div className='chart_year_container'>
        <select value={selectedSorting} onChange={handleSortingSelection} style={{marginRight:"10px"}}>
                    <option value="Import">Virtual Water Import</option>
                    <option value="Export">Virtual Water Export</option>
                </select>

            <select value={selectedYear} onChange={handleYearSelection}>
                <option value="2016">2016</option>
                <option value="2017">2017</option>
                <option value="2018">2018</option>
                <option value="2019">2019</option>
            </select>

        </div>
            <Chart
                options={{
                    chart: {
                        type: 'bar',
                        // stacked: true,
                        toolbar: {
                            show: false,
                        },
                        zoom: {
                            enabled: true,
                        },
                    },
                    // plotOptions: {
                    //     bar: {
                    //         horizontal: true,
                    //     },
                    // },
                    plotOptions: {
                        bar: {
                            horizontal: true,
                            dataLabels: {
                                position: 'top',
                            },
                        }
                    },
                    dataLabels: {
                        enabled: false,
                    },
                    xaxis: {
                        categories: categories,
                        title: {
                            text: `Country-to-Country Green Water Flow (MCM) in ${selectedYear}`,
                            offsetX: 10,
                        },
                    },
                    yaxis: {
                        title: {
                            text: 'Country Name',
                            offsetY: 10,
                        },
                        // reversed: true
                    },

                    tooltip: {
                        y: {
                            formatter: function (val) {
                                return `${val} (MCM/year)`;
                            },
                        },
                    },
                    responsive: [
                        {
                            breakpoint: 480,
                            options: {
                                legend: {
                                    position: 'bottom',
                                    offsetX: -10,
                                    offsetY: 0,
                                },
                            },
                        },
                    ],
                }}
                series={series}
                type='bar'
                width='100%'
                height='500px'
            // height={minHeight + 'px'}
            />
        </>

    );
};

export default CTCGreenWaterFlowChart;
